body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-open-sans: "Open Sans";

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-base: 16px;
  --font-size-sm: 14px;
  --font-size-lg: 18px;

  /* Colors */
  --color-whitesmoke-100: #f8f8f8;
  --color-whitesmoke-200: #f3f3f5;
  --color-white: #fff;
  --color-darkslategray-100: #025159;
  --color-darkslategray-200: #333;
  --color-darkgray-100: #afafaf;
  --color-lightgray-100: #d2d2d2;
  --color-gray-100: #878787;
  --color-gray-200: rgba(255, 255, 255, 0.6);
  --color-red-100: #ff0000;
  --color-gainsboro: #d9d9d9;

  /* Gaps */
  --gap-lg: 18px;
  --gap-9xs: 4px;
  --gap-7xs: 6px;
  --gap-29xl: 48px;
  --gap-6xs: 7px;
  --gap-base: 16px;
  --gap-11xs: 2px;
  --gap-5xl: 24px;

  /* Paddings */
  --padding-3xs-6: 9.6px;
  --padding-12xs-4: 0.4px;

  /* border radiuses */
  --br-7xs: 6px;
  --br-12xs: 1px;
}
